"use client";

import Link from "next/link";
import { useState, useEffect } from "react";

export default function NavBar() {
    const [language, setLanguage] = useState("en");

    useEffect(() => {
        const savedLang = localStorage.getItem("language") || "en";
        setLanguage(savedLang);
    }, []);

    const toggleLanguage = () => {
        const newLang = language === "en" ? "es" : "en";
        setLanguage(newLang);
        localStorage.setItem("language", newLang);
    };

    return (
        <nav className="flex items-center justify-between px-6 py-4 bg-white shadow-sm">
            <div className="text-xl font-bold">AI Training</div>

            <div className="flex items-center space-x-6">
                <a href="/dashboard" className="text-gray-700 hover:text-blue-600">
                    {language === "en" ? "Dashboard" : "Panel"}
                </a>
                <a href="/module1" className="text-gray-700 hover:text-blue-600">
                    {language === "en" ? "Module 1" : "MÃ³dulo 1"}
                </a>

                {/* Language Toggle */}
                <button
                    onClick={toggleLanguage}
                    className="px-3 py-1 text-sm font-semibold border border-gray-300 rounded-full hover:bg-gray-100 transition"
                >
                    {language === "en" ? "ES" : "EN"}
                </button>
            </div>
        </nav>
    );
}